export class Profile {
    private loginstate: string;
    private loginscope: string;
    private impersonation: boolean;
    private technicalID: string;
    private postalcode: string;
    private installationPostalCode: string;
    private billingPostalCode: string;
    private customerCategory: string;
    private logintype: string;
    private customerType: string;

    setLoginState = (loginstate: string): void => {
        this.loginstate = loginstate;
    };

    getLoginState = (): string => {
        return this.loginstate;
    };

    setLoginScope = (loginscope: string): void => {
        this.loginscope = loginscope;
    };

    getLoginScope = (): string => {
        return this.loginscope;
    };

    setImpersonation = (impersonation: boolean): void => {
        this.impersonation = impersonation;
    };

    getImpersonation = (): boolean => {
        return this.impersonation;
    };

    setTechnicalID = (technicalID: string): void => {
        this.technicalID = technicalID;
    };

    getTechnicalID = (): string => {
        return this.technicalID;
    };

    setPostalCode = (postalcode: string): void => {
        this.postalcode = postalcode;
    };

    getPostalCode = (): string => {
        return this.postalcode;
    };

    setInstallationPostalCode = (installationPostalCode: string): void => {
        this.installationPostalCode = installationPostalCode;
    };

    getInstallationPostalCode = (): string => {
        return this.installationPostalCode;
    };

    setBillingPostalCode = (billingPostalCode: string): void => {
        this.billingPostalCode = billingPostalCode;
    };

    getBillingPostalCode = (): string => {
        return this.billingPostalCode;
    };

    setCustomerCategory = (customerCategory: string): void => {
        this.customerCategory = customerCategory;
    };

    getCustomerCategory = (): string => {
        return this.customerCategory;
    };

    setLoginType = (logintype: string): void => {
        this.logintype = logintype;
    };

    getLoginType = (): string => {
        return this.logintype;
    };

    setCustomerType = (customerType: string): void => {
        this.customerType = customerType;
    };

    getCustomerType = (): string => {
        return this.customerType;
    };
}

export class ProfileBuilder {
    private profile = new Profile();

    public withLoginState(loginstate: string): ProfileBuilder {
        this.profile.setLoginState(loginstate);
        return this;
    }

    public withLoginScope(loginscope: string): ProfileBuilder {
        this.profile.setLoginScope(loginscope);
        return this;
    }

    public withImpersonation(impersonation: boolean): ProfileBuilder {
        this.profile.setImpersonation(impersonation);
        return this;
    }

    public withTechnicalID(technicalID: string): ProfileBuilder {
        this.profile.setTechnicalID(technicalID);
        return this;
    };

    public withPostalCode(postalcode: string): ProfileBuilder {
        this.profile.setPostalCode(postalcode);
        return this;
    };

    public withInstallationPostalCode(installationPostalCode: string): ProfileBuilder {
        this.profile.setInstallationPostalCode(installationPostalCode);
        return this;
    };

    public withBillingPostalCode(billingPostalCode: string): ProfileBuilder {
        this.profile.setBillingPostalCode(billingPostalCode);
        return this;
    };

    public withCustomerCategory(customerCategory: string): ProfileBuilder {
        this.profile.setCustomerCategory(customerCategory);
        return this;
    };

    public withLoginType(logintype: string): ProfileBuilder {
        this.profile.setLoginType(logintype);
        return this;
    };

    public withCustomerType (customerType: string): ProfileBuilder {
        this.profile.setCustomerType(customerType);
        return this;
    };

    build(): Profile {
        return this.profile;
    }

}
