export class ProductHolding {
    private productID: string;
    private tvBoxType: string;
    private services: string;
    private loggedInUserProductCategory: string;

    setProductID = (productID: string): void => {
        this.productID = productID;
    };
    getProductID = (): string => {
        return this.productID;
    };

    setTvBoxType = (tvBoxType: string): void => {
        this.tvBoxType = tvBoxType;
    };

    getTvBoxType = (): string => {
        return this.tvBoxType;
    };

    setServices = (services: string): void => {
        this.services = services;
    };

    getServices = (): string => {
        return this.services;
    };

    setLoggedInUserProductCategory = (loggedInUserProductCategory: string): void => {
        this.loggedInUserProductCategory = loggedInUserProductCategory;
    };

    getLoggedInUserProductCategory = (): string => {
        return this.loggedInUserProductCategory;
    };
}

export class ProductHoldingBuilder {
    private productHolding: ProductHolding;

    constructor() {
        this.productHolding = new ProductHolding();
    }

    withProductId(productId: string): ProductHoldingBuilder {
        this.productHolding.setProductID(productId);
        return this;
    }

    withTvBoxType(tvBoxType: string): ProductHoldingBuilder {
        this.productHolding.setTvBoxType(tvBoxType);
        return this;
    }

    withServices(services: string): ProductHoldingBuilder {
        this.productHolding.setServices(services);
        return this;
    }

    withLoggedInUserProductCategory(loggedInUserProductCategory: string): ProductHoldingBuilder {
        this.productHolding.setLoggedInUserProductCategory(loggedInUserProductCategory);
        return this;
    }

    build(): ProductHolding {
        return this.productHolding;
    }
}
