export class LoggingService {

    private perfLogEvents: PerfLogEvent[] = [];
    private isLoggingEnabled: boolean;

    constructor() {
        this.isLoggingEnabled = window.sessionStorage.getItem('udl.logging') === 'enable';
    }

    log = (logEvent: any): void => {
        if (this.isLoggingEnabled) {
            const d = new Date();
            const at: string = d.getMinutes() + 'min:' + d.getSeconds() + 'sec:' + d.getMilliseconds() + 'ms';
            console.log(at, JSON.stringify(logEvent));
        }
    }
    
    startPerflog = (identifier: string): void => {
        if ( this.isLoggingEnabled ){
            const perfLogEvent = this.perfLogEvents.find((item) => {
                return item.identifier === identifier;
            }) || new PerfLogEvent();

            perfLogEvent.identifier = identifier;
            perfLogEvent.startTime = this.getLoggingTime();
            this.perfLogEvents.push(perfLogEvent);
        }
    }

    stopPerflog = (identifier: string): void => {
        if ( this.isLoggingEnabled ){
            const perfLogEvent = this.perfLogEvents.find((item) => {
                return item.identifier === identifier;
            });

            if(perfLogEvent) {
                perfLogEvent.stopTime = this.getLoggingTime();
                const timeTake = perfLogEvent.stopTime - perfLogEvent.startTime
                this.log({msg: 'execution time taken by identifier: '+ identifier, time: timeTake + 'ms'});
            } else {
                this.log({msg: 'execution time taken by identifier: '+ identifier, time: 'Start event not logged'});
            }
        }
    }

    private getLoggingTime = (): number => {
        return Date.now();
    }
}

class PerfLogEvent {
    identifier: string;
    startTime: number = 0;
    stopTime?: number;
}