import { PageInfo } from './pageInfo';
import { Base } from './base';
import { DirectCallLabelEnum } from '../enums/direct-call-label.enum';

export class Page extends Base {
    static PAGE_IDENTIFIER = 'page-identifier';
    private pageInfo: PageInfo;

    constructor(page?: any) {
        super(DirectCallLabelEnum.PAGE_LOADED);

        (<any>Object).assign(this, page);
        this.setIdentifier(Page.PAGE_IDENTIFIER);
        this.pageInfo = page ? new PageInfo(page.pageInfo) : new PageInfo();
    }

    getPageInfo = (): PageInfo => {
        return this.pageInfo;
    };
}

export class PageBuilder {
    private page: Page;

    constructor(page: Page) {
        this.page = new Page(page);
    }

    withAttribute(key: string, value: string | number | boolean): PageBuilder {
        this.page.addAttribute(key, value);
        return this;
    }

    withDirectCallLabel(directCallLabel: DirectCallLabelEnum): PageBuilder {
        this.page.setDirectCallLabel(directCallLabel);
        return this;
    }

    build(): Page {
        return this.page;
    }
}
