export class Utility {        
    static getUUID(): string{
        var dt = new Date().getTime();
        var uuidentifier = 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function(c) {
            var r = (dt + Math.random()*16)%16 | 0;
            dt = Math.floor(dt/16);
            return (c=='x' ? r :(r&0x3|0x8)).toString(16);
        });
        return uuidentifier;
    }

    static getCurrentDateTime(){
        let formattedDate;
        let currentDate = new Date();           
        let timeZoneOffset = -currentDate.getTimezoneOffset(),
            differenceSign = timeZoneOffset >= 0 ? '+' : '-',
            pad = (num)=>{                    
                return (Math.floor(Math.abs(num)) < 10 ? '0' : '') + Math.floor(Math.abs(num));
            };
            formattedDate = currentDate.getFullYear() +
            '-' + pad(currentDate.getMonth() + 1) +
            '-' + pad(currentDate.getDate()) +
            'T' + pad(currentDate.getHours()) +
            ':' + pad(currentDate.getMinutes()) +
            ':' + pad(currentDate.getSeconds()) +
            differenceSign + pad(timeZoneOffset / 60) + pad(timeZoneOffset % 60);                                                        
        return formattedDate;        
    }

    static getItemFromStore(name: string): any {
        return window.sessionStorage.getItem(name);
    }

    static setItemInStore(name: string, value: any): void {
        window.sessionStorage.setItem(name, JSON.stringify(value));
    }    
}