
export class Product {
    
    private productID: string;
    private productName: string;
    private productType: string = 'NotAvailable';
    private productStatus: string;
    private productStock: string;

    constructor(productJson?: any) {
        (<any>Object).assign(this, productJson);
    }

    setProductID = (id: string): void => {
        this.productID = id;
    };
    getProductID = (): string => {
        return this.productID;
    };

    setProductName = (name: string): void => {
        this.productName = name;
    };
    getProductName = (): string => {
        return this.productName;
    };

    setProductType = (type: string): void => {
        this.productType = type;
    };
    getProductType = (): string => {
        return this.productType;
    };
    
    setProductStatus = (productStatus: string): void => {
        this.productStatus = productStatus;
    };
    getProductStatus = (): string => {
        return this.productStatus;
    };

    setProductStock = (productStock: string): void => {
        this.productStock = productStock;
    };
    getProductStock = (): string => {
        return this.productStock;
    };
}

export class ProductBuilder {
    private product: Product;

    constructor() {
        this.product = new Product();
    }

    withProductId(id: string): ProductBuilder {
        this.product.setProductID(id);
        return this;
    }

    withProductName(name: string): ProductBuilder {
        this.product.setProductName(name);
        return this;
    }

    withProductType(type: string): ProductBuilder {
        this.product.setProductType(type);
        return this;
    }

    withProductStatus(status: string): ProductBuilder {
        this.product.setProductStatus(status);
        return this;
    }

    withProductStock(stock: string): ProductBuilder {
        this.product.setProductStock(stock);
        return this;
    }

    build(): Product {
        return this.product;
    }
}
