import { Utility } from '../util/utility';

export class EventInfo {
    private eventName: string;
    private eventStatus: string;
    private eventStatusMessage: string;
    private type: string;
    private timeStamp: string;

    constructor(eventInfoJson?: any) {
        (<any>Object).assign(this, eventInfoJson);
        this.timeStamp = Utility.getCurrentDateTime();
    }

    setEventName = (eventName: string): void => {
        this.eventName = eventName;
    };
    getEventName = (): string => {
        return this.eventName;
    };

    setEventStatus = (eventStatus: string) => {
        this.eventStatus = eventStatus;
    };
    getEventStatus = (): string => {
        return this.eventStatus;
    };

    setEventStatusMessage = (eventStatusMessage: string) => {
        this.eventStatusMessage = eventStatusMessage;
    };
    getEventStatusMessage = (): string => {
        return this.eventStatusMessage;
    };

    setType = (eventType: string) => {
        this.type = eventType;
    };
    getType = (): string => {
        return this.type;
    };

    setTimestamp = (eventTimestamp: string) => {
        this.timeStamp = eventTimestamp;
    };
    getTimestamp = (): string => {
        return this.timeStamp;
    };
}

export class EventInfoBuilder {
    private eventInfo: EventInfo;

    constructor() {
        this.eventInfo = new EventInfo();
    }

    withEventName(name: string): EventInfoBuilder {
        this.eventInfo.setEventName(name);
        return this;
    }

    withType(type: string): EventInfoBuilder {
        this.eventInfo.setType(type);
        return this;
    }

    withEventStatus(status: string): EventInfoBuilder {
        this.eventInfo.setEventStatus(status);
        return this;
    }

    withEventStatusMessage(statusMessage: string): EventInfoBuilder {
        this.eventInfo.setEventStatusMessage(statusMessage);
        return this;
    }

    withTimestamp(timestamp: string): EventInfoBuilder {
        this.eventInfo.setTimestamp(timestamp);
        return this;
    }

    build(): EventInfo {
        return this.eventInfo;
    }
}
