import { ComponentInfo } from './componentInfo';
import { Category } from './category';

export class Component {
    private attributes: any;
    private componentInfo: ComponentInfo;
    private category: Category;

    constructor(component?: any) {
        (<any>Object).assign(this, component);
        this.componentInfo = component ? new ComponentInfo(component.componentInfo) : new ComponentInfo();
        this.category = component ? new Category(component.category) : new Category();
    }

    getComponentInfo(): ComponentInfo {
        return this.componentInfo;
    };

    getAttribute = (name: string) => {
        if (this.attributes) {
            return this.attributes[name];
        }
    };
    addAttribute = (name, value) => {
        if (!this.attributes) {
            this.attributes = {};
        }
        this.attributes[name] = value;
    };

    getCategory = (): Category => {
        return this.category;
    };
}

export class ComponentBuilder {
    private component: Component;

    constructor(component?: Component) {
        this.component = new Component(component);
    }

    withAttribute(key: string, value: any): ComponentBuilder {
        this.component.addAttribute(key, value);
        return this;
    }

    build(): Component {
        return this.component;
    }
}
