import { Segment } from "./segment";
import { Profile } from "./profile";
import { ProductHolding } from "./productHolding";
import { Billing } from "./billing";
import { Pega } from "./pega";
import { Base } from "./base";
import { DirectCallLabelEnum } from "../enums/direct-call-label.enum";

export class User extends Base {
    private segment: Segment;
    private profile: Profile;
    private productHolding: ProductHolding;
    private billing: Billing;
    private pega: Pega;

    constructor(){
        super(DirectCallLabelEnum.USER_DATA_LOADED);
    }

    setSegment = (segment: Segment): void => {
        this.segment = segment;
    };
    getSegment = (): Segment => {
        return this.segment;
    };

    setProfile = (profile: Profile) :void => {
        this.profile = profile;
    };
    getProfile = () :Profile => {
        return this.profile;
    };

    setProductHolding = (productHolding: ProductHolding): void => {
        this.productHolding = productHolding;
    };
    getProductHolding = (): ProductHolding => {
        return this.productHolding;
    };

    setBilling = (billing: Billing): void => {
        this.billing = billing;
    };
    getBilling = (): Billing => {
        return this.billing;
    };

    setPega = (pega: Pega): void => {
        this.pega = pega;
    };
    getPega = (): Pega => {
        return this.pega;
    };    
}

export class UserBuilder {
    private user = new User();

    withSegment(segment: Segment): UserBuilder {
        this.user.setSegment(segment);
        return this;
    }

    withProfile(profile: Profile): UserBuilder {
        this.user.setProfile(profile);
        return this;
    }

    withProductHolding(productHolding: ProductHolding): UserBuilder {
        this.user.setProductHolding(productHolding);
        return this;
    }

    withDirectCallLabel(directCallLabel: DirectCallLabelEnum): UserBuilder {
        this.user.setDirectCallLabel(directCallLabel)
        return this;
    }

    withBilling(billing: Billing): UserBuilder {
        this.user.setBilling(billing);
        return this;
    }

    withPega(pega: Pega): UserBuilder {
        this.user.setPega(pega);
        return this;
    }

    build(): User {
        return this.user;
    }
}

