import { Page } from "../models/page";
import { AnalyticsBeaconEnum } from "../enums/analytics-beacon.enum";
import { LoggingService } from "./logging/loggingService";
import { Base } from "../models/base";
import { ParkedAnalyticsObjectInterface } from "../interfaces/parked-analytics-object.interface";
import { AnalyticsConstants } from "../constants/analytics.constant";
import { AnalyticsCallFireStrategyEnum } from "../enums/analytics-call-fire-strategy.enum";
import { Utility } from "../util/utility";
import { Deferred } from "./deferred";

export class AnalyticsService {
    static TIMEOUT_IF_SATELLITE_NOT_LOADED: number = 6000; // ms

    private analyticsPromises: any = {};
    private loggingService: LoggingService;
    private satelliteCallbacks = [];

    constructor(private analyticsBeacons?: AnalyticsBeaconEnum) {

        this.loggingService = new LoggingService();
     
        this.createSatelliteLoadListener();

        if (!this.analyticsBeacons) {
            this.analyticsBeacons = AnalyticsBeaconEnum.ENABLED;
        }
    }

    private createSatelliteLoadListener(): void {
        const analyticsService = this;

        let _isSatelliteLoaded = window['isSatelliteLoaded'];
        Object.defineProperty(window, 'isSatelliteLoaded', {
            configurable: true,
            enumerable: true,
            get: function() {
                return _isSatelliteLoaded;
            },
            set: function(satelliteLoaded) {
                _isSatelliteLoaded = satelliteLoaded;
                
                const len = analyticsService.satelliteCallbacks.length;
                for(let i = 0; i < len;  i++) {
                    const callback = analyticsService.satelliteCallbacks.shift();
                    callback();
                }                 
            }
        });
    }

    pushDataToAnalyticsWithoutWait(base: Base, doNotFirePageBeacon?: boolean): Promise<boolean> {
        const baseBeaconDeferred = this.getOrCreateDeferred(base.getIdentifier());

        const pushDataCallback = () => {
            this.pushDataToAnalyticsWithoutWaitInternal(base, doNotFirePageBeacon);
        }

        // Force resolve promise in case _satellite took long time to load
        const resolvePromiseIfSatelliteNotLoadedCallback = () => {
            this.loggingService.log({msg: 'Timer with ' + AnalyticsService.TIMEOUT_IF_SATELLITE_NOT_LOADED + 'ms started to force resolve identifier: ' + base.getIdentifier() });
            this.resolvePromiseWithDelay(base.getIdentifier(), AnalyticsService.TIMEOUT_IF_SATELLITE_NOT_LOADED);
        }; 

        this.onSatelliteLoaded(pushDataCallback, resolvePromiseIfSatelliteNotLoadedCallback);
        
        return baseBeaconDeferred.promise;
    }

    private pushDataToAnalyticsWithoutWaitInternal(base: Base, doNotFirePageBeacon?: boolean): void {
        if (doNotFirePageBeacon) {
            return;
        }
        this.fireAnalyticsBeacon(base);
    }

    private onSatelliteLoaded(callback, resolvePromiseIfSatelliteNotLoadedCallback): void {
        if (this.isSatelliteLoaded()) {
            this.loggingService.log({msg: '_satellite is loaded!!'});
            callback();
            return;
        } 
        this.loggingService.log({msg: '_satellite is not loaded. Waiting ........'});
        
        this.satelliteCallbacks.push(callback);        
        resolvePromiseIfSatelliteNotLoadedCallback();
    }

    private isSatelliteLoaded(): boolean {
        return window['isSatelliteLoaded'];
    }

    pushDataToAnalyticsWithWait(analyticsDataObj: Base): Promise<boolean> {
        const analyticsDeferred = this.getOrCreateDeferred(analyticsDataObj.getIdentifier());

        switch (analyticsDataObj.getStrategy()) {
            case AnalyticsCallFireStrategyEnum.FIRE_ON_NEXT_PAGE:
            case AnalyticsCallFireStrategyEnum.FIRE_ON_PAGE_RELOAD:
                this.parkAnalyticsObject(analyticsDataObj);
                this.resolvePromiseNow(analyticsDataObj.getIdentifier());
                break;
            default:
                const pageBeaconPromise = this.getPageBeaconPromise();
                ((analyticsObj) => {
                    pageBeaconPromise.then(() => {
                        this.fireAnalyticsBeacon(analyticsObj);
                    });
                })(analyticsDataObj);
        }
        return analyticsDeferred.promise;
    }

    private parkAnalyticsObject(analyticsDataObj: Base): void {
        const parkedAnalyticsObjectsInStore = Utility.getItemFromStore(AnalyticsConstants.STORE_KEY_PARKED_EVENTS);
        const parkedAnalyticsObjects =  parkedAnalyticsObjectsInStore ? JSON.parse(parkedAnalyticsObjectsInStore) : [];

        const analyticsObjectToPark: ParkedAnalyticsObjectInterface = {
            'parkedObject': analyticsDataObj,
            'parkedOnPage' : window.location.pathname
        };
        parkedAnalyticsObjects.push(analyticsObjectToPark);

        Utility.setItemInStore(AnalyticsConstants.STORE_KEY_PARKED_EVENTS, parkedAnalyticsObjects);
    }        

    getAnalyticsPromises(): any {
        return this.analyticsPromises;
    }

    private getOrCreateDeferred(promiseIdentifier: string): Deferred {
        if (!promiseIdentifier) {
            return;
        }
        
        let analyticsDeferred: Deferred = this.analyticsPromises[promiseIdentifier];
        if (analyticsDeferred === undefined) {
            analyticsDeferred = new Deferred();
            this.analyticsPromises[promiseIdentifier] = analyticsDeferred;
        }
        return analyticsDeferred;
    }

    public getPageBeaconPromise(): Promise<boolean> {
        return this.getOrCreateDeferred(Page.PAGE_IDENTIFIER).promise;
    }

    private fireAnalyticsBeacon(base: Base): boolean {
        this.loggingService.log({msg: 'invoking launch direct call: ' + base.getDirectCallLabel(),
                                identifier: base.getIdentifier()});

        let isAnalyticsBeaconFired = false;
        if (this.analyticsBeacons === AnalyticsBeaconEnum.ENABLED) {
            try {
                window['_satellite'].track(base.getDirectCallLabel(), { identifier: base.getIdentifier() });
                isAnalyticsBeaconFired = true;
                this.loggingService.log({msg: 'direct call invoked!', identifier: base.getIdentifier()});
            } catch (err) {
                console.log("Analytics direct call invocation failed", err);
            }
        }
        this.resolvePromiseNow(base.getIdentifier());

        return isAnalyticsBeaconFired;
    }

    private resolvePromiseWithDelay(promiseIdentifier: string, duration: number): void {
        setTimeout(
            () => {
                this.resolvePromiseNow(promiseIdentifier);
            },
            duration
        );
    }

    public resolvePromiseNow(promiseIdentifier: string): void {
        const deferred = this.analyticsPromises[promiseIdentifier];
        if (deferred) {
            deferred.resolve(true);

            this.loggingService.log({msg: 'promise resolved', identifier: promiseIdentifier});
            
            if (promiseIdentifier !== Page.PAGE_IDENTIFIER) {
                delete this.analyticsPromises[promiseIdentifier];
            }
        }
    }
}
