import { Price } from "./price";
import { Item } from "./item";
import { Base } from "./base";
import { DirectCallLabelEnum } from "../enums/direct-call-label.enum";
import { AnalyticsCallFireStrategyEnum } from "../enums/analytics-call-fire-strategy.enum";

export class Transaction extends Base {
    private transactionID: string;
    private price: Price;
    private item: Item[];
    private existingItem: Item[];

    constructor(transactionJson?: any) {
        super(DirectCallLabelEnum.TRANSACTION_UPDATED);
        if (transactionJson) {
            (<any>Object).assign(this, transactionJson);
            this.price = transactionJson.price ? new Price(transactionJson.price) : undefined;
            if (transactionJson.item) {
                this.setItems([]);
                transactionJson.item.forEach((itemJson) => {
                    let item = new Item(itemJson);
                    this.addItem(item);
                });
            }
            if (transactionJson.existingItem) {
                this.setExistingItems([]);
                transactionJson.existingItem.forEach((itemJson) => {
                    let item = new Item(itemJson);
                    this.addExistingItem(item);
                });
            }
        }
    }

    setTransactionID = (transactionID: string) => {
        this.transactionID = transactionID;
    };
    getTransactionID = (): string => {
        return this.transactionID;
    };

    setPrice = (price: Price): void => {
        this.price = price;
    };
    getPrice = (): Price => {
        return this.price;
    };

    addItem = (item: Item): void => {
        if (!this.item)
            this.item = [];
        this.item.push(item);
    };
    getItems = (): Item[] => {
        return this.item;
    };
    setItems = (items: Item[]): void => {
        this.item = items;
    };

    addExistingItem = (item: Item): void => {
        if (!this.existingItem)
            this.existingItem = [];
        this.existingItem.push(item);
    };
    getExistingItems = (): Item[] => {
        return this.existingItem;
    };
    setExistingItems = (existingItem: Item[]): void => {
        this.existingItem = existingItem;
    };

}

export class TransactionBuilder {
    private transaction = new Transaction();

    withTransactionID(transactionID: string): TransactionBuilder {
        this.transaction.setTransactionID(transactionID);
        return this;
    }

    withPrice(price: Price): TransactionBuilder {
        this.transaction.setPrice(price);
        return this;
    }

    withItem(item: Item): TransactionBuilder {
        this.transaction.addItem(item);
        return this;
    }

    withItems(items: Item[]): TransactionBuilder {
        this.transaction.setItems(items)
        return this;
    }

    withExistingItem(item: Item): TransactionBuilder {
        this.transaction.addExistingItem(item);
        return this;
    }

    withExistingItems(items: Item[]): TransactionBuilder {
        this.transaction.setExistingItems(items)
        return this;
    }

    withAttribute(name: string, value: string|number|boolean): TransactionBuilder {
        this.transaction.addAttribute(name, value);
        return this;
    }

    withAttributes(items: Item[]): TransactionBuilder {
        this.transaction.setAttributes(items);
        return this;
    }

    withDirectCallLabel(directCallLabel: DirectCallLabelEnum): TransactionBuilder {
        this.transaction.setDirectCallLabel(directCallLabel)
        return this;
    }

    withStrategy(strategy: AnalyticsCallFireStrategyEnum): TransactionBuilder {
        this.transaction.setStrategy(strategy)
        return this;
    }

    build(): Transaction {
        return this.transaction;
    }
}
