import { EventInfo } from "./eventInfo";
import { Category } from "./category";
import { Base } from "./base";
import { DirectCallLabelEnum } from "../enums/direct-call-label.enum";
import { AnalyticsCallFireStrategyEnum } from "../enums/analytics-call-fire-strategy.enum";
import { Cart } from "./cart";

export class Event extends Base {
    private eventInfo: EventInfo;
    private category: Category;
    
    constructor(eventJson?: any) {
        super(DirectCallLabelEnum.EVENT_OCCURRED);
        if (eventJson) {
            (<any>Object).assign(this, eventJson);
            this.eventInfo = eventJson.eventInfo ? new EventInfo(eventJson.eventInfo) : undefined;
            this.category = eventJson.category ? new Category(eventJson.category) : undefined;

            Object.keys(eventJson.attributes || {}).forEach((attributeKey) => {
                if (typeof eventJson.attributes[attributeKey] === 'object') {
                    if (eventJson.attributes[attributeKey].directCallLabel === DirectCallLabelEnum.CART_VIEWED) {
                            const enrichedCart = new Cart(eventJson.attributes[attributeKey]);
                            this.addAttribute(attributeKey, enrichedCart)
                    }
                }
            });
        }
    }

    setEventInfo = (eventInfo: EventInfo): void => {
        this.eventInfo = eventInfo;            
    };
    getEventInfo = (): EventInfo => {
        return this.eventInfo;
    };

    setCategory = (category: Category) : void=> {
        if(category)
            this.category = category;                
    };
    getCategory =  (): Category =>{
            return this.category;
    };

}

export class EventBuilder {
    private event: Event;

    constructor() {
        this.event = new Event();
    }

    withEventInfo(eventInfo: EventInfo): EventBuilder {
        this.event.setEventInfo(eventInfo);
        return this;
    }

    withCategory(category: Category): EventBuilder {
        this.event.setCategory(category);
        return this;
    }

    withAttributes(attributes: any): EventBuilder {
        this.event.setAttributes(attributes);
        return this;
    }

    withStrategy(strategy: AnalyticsCallFireStrategyEnum): EventBuilder {
        this.event.setStrategy(strategy)
        return this;
    }

    build(): Event {
        return this.event;
    }
}
