import { AnalyticsCallFireStrategyEnum } from "../enums/analytics-call-fire-strategy.enum";
import { DirectCallLabelEnum } from "../enums/direct-call-label.enum";
import { Utility } from "../util/utility";

export class Base {
    private directCallLabel: DirectCallLabelEnum;
    private identifier: string;
    private attributes: any = {}
    private forceResolvePromiseAfterDelay: number = 0; // circuitBreakerTimeoutInMS
    private strategy: AnalyticsCallFireStrategyEnum = AnalyticsCallFireStrategyEnum.FIRE_ON_SAME_PAGE;

    constructor(directCallLabel: DirectCallLabelEnum) {
        this.directCallLabel = directCallLabel;
        this.identifier = Utility.getUUID();
    }

    setIdentifier = (identifier: string) => {
        this.identifier = identifier;
    };
    getIdentifier = (): string => {
        return this.identifier;
    };

    addAttribute = (name: string, value: string | number | boolean | Base): void => {
        this.attributes[name] = value;
    };
    getAttribute = (name: string): string => {
        if (this.attributes) {
            return this.attributes[name];
        }
    };
    setAttributes = (attributes: any): void => {
        this.attributes = attributes;
    };
    getAttributes = () =>{
        return this.attributes;
    };

    setDirectCallLabel = (directCallLabel: DirectCallLabelEnum): void => {
        this.directCallLabel = directCallLabel;
    };
    getDirectCallLabel = (): DirectCallLabelEnum => {
        return this.directCallLabel;
    };

    setForceResolvePromiseAfterDelay = (timeoutInMS: number) => {
        this.forceResolvePromiseAfterDelay = timeoutInMS;
    };
    getForceResolvePromiseAfterDelay = (): number => {
        return this.forceResolvePromiseAfterDelay;
    };

    setStrategy = (strategy: AnalyticsCallFireStrategyEnum): void => {
        this.strategy = strategy;
    };
    getStrategy = (): AnalyticsCallFireStrategyEnum => {
        return this.strategy;
    };
}
