export class Deferred {
    private _promise: Promise<boolean>;
    private _resolve: any;
    private _reject: any;

    constructor() {
        this._promise = new Promise((resolve, reject) => {
                          this._reject = reject
                          this._resolve = resolve
                        });
    }

    public get promise() {
      return this._promise;
    }
    public get resolve() {
      return this._resolve;
    }
    public get reject() {
      return this._reject;
    }        
  }