export class Price {
    private onetime: number;
    private recurring: number;
    private summaryPrice: number;
    private basePrice: Price;
    private total: Price;
    private discounted: Price;
    private discountRecurring: Price;

    constructor(priceJson?: any) {
        if (priceJson) {
            (<any>Object).assign(this, priceJson);
            this.basePrice = priceJson.basePrice ? new Price(priceJson.basePrice) : undefined;
            this.total = priceJson.total ? new Price(priceJson.total) : undefined;
            this.discounted = priceJson.discounted ? new Price(priceJson.discounted) : undefined;
            this.discountRecurring = priceJson.discountRecurring ? new Price(priceJson.discountRecurring) : undefined;
        }
    }

    setOnetimePrice = (onetime: number): void => {
        this.onetime = onetime;
    };
    getOnetimePrice = (): number => {
        return this.onetime;
    };

    setRecurringPrice = (recurring: number): void => {
        this.recurring = recurring;
    };
    getRecurringPrice = (): number => {
        return this.recurring;
    };

    setBasePrice = (basePrice: Price): void => {
        this.basePrice = basePrice;
    };
    getBasePrice = (): Price => {
        return this.basePrice;
    };

    setTotalPrice = (totalPrice: Price): void => {
        this.total = totalPrice;
    };
    getTotalPrice = (): Price => {
        return this.total;
    };

    setDiscountedPrice = (discountedPrice: Price): void => {
        this.discounted = discountedPrice;
    };
    getDiscountedPrice = (): Price => {
        return this.discounted;
    };

    setTotalDiscountRecurring = (discountRecurring: Price): void => {
        this.discountRecurring = discountRecurring;
    };
    getTotalDiscountRecurring = (): Price => {
        return this.discountRecurring;
    };

    setSummaryPrice = (summary: number): void => {
        this.summaryPrice = summary;
    };
    getSummaryPrice = (): number => {
        return this.summaryPrice;
    };
}

export class PriceBuilder {
    private price: Price;

    constructor() {
        this.price = new Price();
    }

    withOneTimePrice(oneTimePrice: number): PriceBuilder {
        this.price.setOnetimePrice(oneTimePrice);
        return this;
    }

    withRecurringPrice(recurringPrice: number): PriceBuilder {
        this.price.setRecurringPrice(recurringPrice);
        return this;
    }

    withBasePrice(basePrice: Price): PriceBuilder {
        this.price.setBasePrice(basePrice);
        return this;
    }

    withTotalPrice(totalPrice: Price): PriceBuilder {
        this.price.setTotalPrice(totalPrice);
        return this;
    }

    withDiscountedPrice(discountedPrice: Price): PriceBuilder {
        this.price.setDiscountedPrice(discountedPrice);
        return this;
    }

    withTotalDiscountRecurring(discountRecurring: Price): PriceBuilder {
        this.price.setTotalDiscountRecurring(discountRecurring);
        return this;
    }

    withSummaryPrice(summaryPrice: number): PriceBuilder {
        this.price.setSummaryPrice(summaryPrice);
        return this;
    }

    build(): Price {
        return this.price;
    }
}
