import { User } from "./user";
import { Component } from "./component";
import { Page } from "./page";
import { Cart } from "./cart";
import { Transaction } from "./transaction";
import { AnalyticsService } from "../services/analyticsService";
import { Utility } from "../util/utility";
import { Category } from "./category";
import { Event } from './event';
import { AnalyticsBeaconEnum } from "../enums/analytics-beacon.enum";
import { DirectCallLabelEnum } from "../enums/direct-call-label.enum";
import { AnalyticsConstants } from "../constants/analytics.constant";
import { ParkedAnalyticsObjectInterface } from "../interfaces/parked-analytics-object.interface";
import { AnalyticsCallFireStrategyEnum } from "../enums/analytics-call-fire-strategy.enum";


export class DataLayer {
    static instance: DataLayer;

    private version: string;
    private dateTimeLocal: string;
    private environment: string;
    private user: User[];
    private component: Component[];
    private brand: string;
    private applicationID: string;
    private page: Page;
    private cart: Cart;
    private transaction: Transaction;
    private event: Event[];

    private analyticsService: AnalyticsService;
    private doNotFirePageBeacon: boolean;

    private constructor(dataLayerJson?: any) {
        if (dataLayerJson) {
            dataLayerJson.initialized = true;
            (<any>Object).assign(this, dataLayerJson);
            this.analyticsService = new AnalyticsService();
            this.dateTimeLocal = Utility.getCurrentDateTime();

            if (dataLayerJson.page) {
                this.page = new Page(dataLayerJson.page);
            }
        }
        else {
            this.analyticsService = new AnalyticsService(AnalyticsBeaconEnum.DISABLED);
        }
    }

    static create(): DataLayer {
        let udlObjectName: string = window['udlObjectName'];
        let dataLayerJson: any = window[udlObjectName];

        if (!(dataLayerJson && dataLayerJson.applicationID && dataLayerJson.environment)) {
            console.error("Missing Mandatory properties!!! Initial DataLayer JSON with applicationID and "
                + " environment properties is mandatory.");
            DataLayer.instance = new DataLayer();
        } else {
            if (dataLayerJson.initialized) {
                DataLayer.instance = dataLayerJson;
            } else {
                DataLayer.instance = new DataLayer(dataLayerJson);
                window[udlObjectName] = DataLayer.instance;
            }
        }
        return DataLayer.instance;
    }

    static destroy(): void {
        DataLayer.instance = null;
    }

    setVersion = (version: string): void => {
        this.version = version;
    };
    getVersion = (): string => {
        return this.version;
    };

    setDateTimeLocal = (dateTime: string): void => {
        this.dateTimeLocal = dateTime;
    };
    getDateTimeLocal = (): string => {
        return this.dateTimeLocal;
    };

    setEnvironment = (env: string): void => {
        this.environment = env;
    };
    getEnvironment = (): string => {
        return this.environment;
    };

    setDoNotFirePageBeacon = (doNotFirePageBeacon: boolean): void => {
        this.doNotFirePageBeacon = doNotFirePageBeacon;
    };
    getDoNotFirePageBeacon = (): boolean => {
        return this.doNotFirePageBeacon;
    };    

    getPage = (): Page => {
        return this.page;
    };
    setPage = (page: Page): Promise<boolean> => {
        this.page = page;
        return this.analyticsService.pushDataToAnalyticsWithoutWait(this.page, this.doNotFirePageBeacon);
    };

    getComponents = (): Component[] => {
        return this.component;
    };
    setComponents = (component: Component[]) => {
        this.component = component;
    };

    getEvents = (): Event[] => {
        return this.event;
    };
    getEventById = (id: string): Event => {
        return this.getEvents().filter(event => event.getIdentifier() === id)[0];
    };

    addUser = (user: User): Promise<boolean> => {
        if (!this.user) {
            this.user = [];
        }
        this.user.push(user);
        return this.analyticsService.pushDataToAnalyticsWithoutWait(user);
    };
    getUsers = (): User[] => {
        return this.user;
    };

    getUserById = (id: string): User => {
        return this.getUsers().filter(user => user.getIdentifier() === id)[0];
    };

    getCart = (): Cart => {
        return this.cart;
    };
    setCart = (cart: Cart): Promise<boolean> => {
        this.cart = cart;
        return this.analyticsService.pushDataToAnalyticsWithWait(cart);
    };

    getTransaction = (): Transaction => {
        return this.transaction;
    };
    setTransaction = (transaction: Transaction): Promise<boolean> => {
        this.transaction = transaction;
        return this.analyticsService.pushDataToAnalyticsWithWait(transaction);
    };

    setBrand = (brand: string): void => {
        this.brand = brand;
    };
    getBrand = (): string => {
        return this.brand;
    };

    setApplicationID = (appId: string): void => {
        this.applicationID = appId;
    };
    getApplicationID = (): string => {
        return this.applicationID;
    };

    getCategory = (componentInstanceID: string): Category => {
        let component = this.getComponentById(componentInstanceID);
        if (component) {
            return component.getCategory();
        }
    };

    getComponentById = (componentInstanceId: string): Component => {
        return this.getComponents() && this.getComponents().length > 0 ?
            this.getComponents().filter((component) => {
                return component.getComponentInfo().getComponentInstanceID() === componentInstanceId
            })[0] :
            undefined;
    };

    addEvent = (event: Event): Promise<boolean> => {
        this.appendEvent(event);
        return this.analyticsService.pushDataToAnalyticsWithWait(event);
    };

    private appendEvent = (event: Event): void => {
        if (!this.event) {
            this.event = [];
        }
        this.event.push(event);
    };

    addStepViewEvent = (event: Event): Promise<boolean> => {
        event.setDirectCallLabel(DirectCallLabelEnum.EVENT_OCCURRED_STEP_VIEW);
        if (this.doNotFirePageBeacon) { // default page-view beacon is disabled
            this.appendEvent(event);

            const stepViewEventPromise = this.analyticsService.pushDataToAnalyticsWithoutWait(event);
            stepViewEventPromise.then(() => {
                this.analyticsService.getPageBeaconPromise();
                this.analyticsService.resolvePromiseNow(this.getPage().getIdentifier());
            });
            return stepViewEventPromise;
        }

        return this.addEvent(event);
    };

    getAnalyticsPromises = (): Promise<boolean>[] => {
        const analyticsPromises = this.analyticsService.getAnalyticsPromises();
        const promises = Object.keys(analyticsPromises).map((item) => {
            return analyticsPromises[item].promise;
        });
        return promises;
    };

    reportParkedObjects = (): void => {
        const pendingParkedAnalyticsObjects = [];
        const parkedAnalyticsObjectsInStore = Utility.getItemFromStore(AnalyticsConstants.STORE_KEY_PARKED_EVENTS);
        const parkedAnalyticsObjects = parkedAnalyticsObjectsInStore ? JSON.parse(parkedAnalyticsObjectsInStore) : [];
        
        parkedAnalyticsObjects.forEach((parkedAnalyticsObject: ParkedAnalyticsObjectInterface) => {
            const parkedJSONObject: any = parkedAnalyticsObject.parkedObject;
            const currentURI = window.location.pathname;
            if (
                (parkedJSONObject.strategy === AnalyticsCallFireStrategyEnum.FIRE_ON_PAGE_RELOAD)
                || (parkedJSONObject.strategy === AnalyticsCallFireStrategyEnum.FIRE_ON_NEXT_PAGE 
                    && currentURI !== parkedAnalyticsObject.parkedOnPage)
            ) {
                    delete  parkedJSONObject.strategy;
                    delete  parkedJSONObject.identifier;
                    this.reportParkedObject(parkedJSONObject);
            } else {
                pendingParkedAnalyticsObjects.push(parkedAnalyticsObject);
            }
        });
        Utility.setItemInStore(AnalyticsConstants.STORE_KEY_PARKED_EVENTS, pendingParkedAnalyticsObjects);
    }

    private reportParkedObject = (parkedJSONObject: any): void => {
        switch (parkedJSONObject.directCallLabel) {
            case DirectCallLabelEnum.EVENT_OCCURRED:
                const parkedEvent = new Event(parkedJSONObject);
                this.addEvent(parkedEvent);
                break;
            case DirectCallLabelEnum.TRANSACTION_UPDATED:
                const parkedTransaction = new Transaction(parkedJSONObject);
                this.setTransaction(parkedTransaction);
        }
    }
}

