export class PageInfo {

    private pageID: string;
    private pageName: string;
    private pageTitle: string;
    private pageType: string;
    private referringURL: string;
    private destinationURL: string;
    private sysEnv: string;
    private language: string;
    private effectiveDate: string;
    private expiryDate: string;
    private issueDate: string;
    private previousPageName: string;
    private stepId: string;

    constructor(pageInfo?: any) {
        (<any>Object).assign(this, pageInfo);
    }

    getPageID = (): string => {
        return this.pageID;
    };
    setPageID = (pageId: string) => {
        this.pageID = pageId;
    };

    setPageName = (pageName: string): void => {
        this.pageName = pageName;
    };
    getPageName = (): string => {
        return this.pageName;
    };

    setPageTitle = (pageTitle: string): void => {
        this.pageTitle = pageTitle;
    };
    getPageTitle = (): string => {
        return this.pageTitle;
    };

    setPageType = (pageType: string): void => {
        this.pageType = pageType;
    };
    getPageType = (): string => {
        return this.pageType;
    };

    setReferringURL = (referringURL: string) => {
        this.referringURL = referringURL;
    };
    getReferringURL = (): string => {
        return this.referringURL;
    };

    setDestinationURL = (destinationURL: string) => {
        this.destinationURL = destinationURL;
    };
    getDestinationURL = (): string => {
        return this.destinationURL;
    };

    setSysEnv = (sysEnv: string) => {
        this.sysEnv = sysEnv;
    };
    getSysEnv = (): string => {
        return this.sysEnv;
    };

    setLanguage = (language: string) => {
        this.language = language;
    };
    getLanguage = (): string => {
        return this.language;
    };

    setEffectiveDate = (date: string) => {
        this.effectiveDate = date;
    };
    getEffectiveDate = (): string => {
        return this.effectiveDate;
    };

    setExpiryDate = (date: string) => {
        this.expiryDate = date;
    };
    getExpiryDate = (): string => {
        return this.expiryDate;
    };

    setIssueDate = (date: string) => {
        this.issueDate = date;
    };
    getIssueDate = (): string => {
        return this.issueDate;
    };

    setPreviousPageName = (previousPageName: string) => {
        this.previousPageName = previousPageName;
    };
    getPreviousPageName = (): string => {
        return this.previousPageName;
    };

    setStepId = (stepId: string) => {
        this.stepId = stepId;
    };
    getStepId = (): string => {
        return this.stepId;
    };
}

export class PageInfoBuilder {
    private pageInfo: PageInfo;

    constructor(pageInfo: PageInfo) {
        this.pageInfo = new PageInfo(pageInfo);
    }

    withPageId(pageId: string): PageInfoBuilder {
        this.pageInfo.setPageID(pageId);
        return this;
    }

    withPageName(pageName: string): PageInfoBuilder {
        this.pageInfo.setPageName(pageName);
        return this;
    }

    withPageTitle(pageTitle: string): PageInfoBuilder {
        this.pageInfo.setPageTitle(pageTitle);
        return this;
    }

    withPageType(pageType: string): PageInfoBuilder {
        this.pageInfo.setPageType(pageType);
        return this;
    }

    withReferringURL(referringURL: string): PageInfoBuilder {
        this.pageInfo.setReferringURL(referringURL);
        return this;
    }

    withDestionationURL(destinationURL: string): PageInfoBuilder {
        this.pageInfo.setDestinationURL(destinationURL);
        return this;
    }

    withSysEnv(sysEnv: string): PageInfoBuilder {
        this.pageInfo.setSysEnv(sysEnv);
        return this;
    }

    withLanguage(language: string): PageInfoBuilder {
        this.pageInfo.setLanguage(language);
        return this;
    }

    withEffectiveDate(date: string): PageInfoBuilder {
        this.pageInfo.setEffectiveDate(date);
        return this;
    }

    withExpiryDate(date: string): PageInfoBuilder {
        this.pageInfo.setExpiryDate(date);
        return this;
    }

    withIssueDate(date: string): PageInfoBuilder {
        this.pageInfo.setIssueDate(date);
        return this;
    }

    withPreviousPageName(previousPageName: string): PageInfoBuilder {
        this.pageInfo.setPreviousPageName(previousPageName);
        return this;
    }

    withStepId(stepId: string): PageInfoBuilder {
        this.pageInfo.setStepId(stepId);
        return this;
    }

    build(): PageInfo {
        return this.pageInfo;
    }
}
