export class Segment {
    private customerSegment: string;

    setCustomerSegment = (customerSegment: string) => {
        this.customerSegment = customerSegment;
    };
    getCustomerSegment = (): string => {
        return this.customerSegment;
    };
}

export class SegmentBuilder {
    private segment = new Segment();

    withCustomerSegment(customerSegment: string): SegmentBuilder {
        this.segment.setCustomerSegment(customerSegment);
        return this;
    }

    build(): Segment {
        return this.segment;
    }
}
