import { Price } from './price';
import { Item } from './item';
import { Base } from './base';
import { DirectCallLabelEnum } from '../enums/direct-call-label.enum';

export class Cart extends Base {
    private cartID: string;
    private price: Price;
    private item: Item[];


    constructor(cartJson?: any) {
        super(DirectCallLabelEnum.CART_VIEWED);
        if (cartJson) {
            (<any>Object).assign(this, cartJson);
            this.price = cartJson.price ? new Price(cartJson.price) : undefined;
            if (cartJson.item) {
                this.setItems([]);
                cartJson.item.forEach((itemJson) => {
                    let item = new Item(itemJson);
                    this.addItem(item);
                });
            }
        }
    }
    setCartID = (id: string): void => {
        this.cartID = id;
    };
    getCartID = (): string => {
        return this.cartID;
    };
    setPrice = (price: Price) => {
        this.price = price;
    };
    getPrice = (): Price => {
        return this.price;
    };
    addItem = (item: Item) => {
        if (!this.item) {
            this.item = [];
        }
        this.item.push(item);
    };
    getItems = () => {
        return this.item;
    };
    setItems = (items) => {
        this.item = items;
    };
    
    filter = (productID: string) => {
        return this.item ? this.item.filter(item => {
            return item.getProductInfo().getProductID() === productID;
        }) : '';
    };
}

export class CartBuilder {
    private cart: Cart;

    constructor() {
        this.cart = new Cart();
    }

    withCartId(cartid: string): CartBuilder {
        this.cart.setCartID(cartid);
        return this;
    }

    withPrice(price: Price): CartBuilder {
        this.cart.setPrice(price);
        return this;
    }

    withItem(item: Item): CartBuilder {
        this.cart.addItem(item);
        return this;
    }

    withIdentifier(identifier: string): CartBuilder {
        this.cart.setIdentifier(identifier);
        return this;
    }

    withAttribute(key: string, value: string | number | boolean): CartBuilder {
        this.cart.addAttribute(key, value);
        return this;
    }

    withDirectCallLabel(directCallLabel: DirectCallLabelEnum): CartBuilder {
        this.cart.setDirectCallLabel(directCallLabel);
        return this;
    }

    build(): Cart {
        return this.cart;
    }
}
