export class Category {
    private primaryCategory: string;
    private subCategory1: string;
    
    constructor(category?: any){
        (<any>Object).assign(this, category);       
    }
    
    setPrimaryCategory = (primaryCategory: string) => {
        this.primaryCategory = primaryCategory;
    };
    getPrimaryCategory = (): string => {
        return this.primaryCategory;
    };

    setSubCategory1 = (subCategory1: string) => {
        this.subCategory1 = subCategory1;
    };
    getSubCategory1 = (): string => {
        return this.subCategory1;
    };
}

export class CategoryBuilder {
    private category = new Category();

    withPrimaryCategory(category: string): CategoryBuilder {
        this.category.setPrimaryCategory(category);
        return this;
    }

    withSubCategoryOne(category: string): CategoryBuilder {
        this.category.setSubCategory1(category);
        return this;
    }

    build(): Category {
        return this.category;
    }
}
