import { Product } from './product';
import { Price } from './price';

export class Item {

    private productInfo: Product;
    private price: Price;
    private quantity: number;

    constructor(itemJson?: any) {
        if (itemJson) {
            (<any>Object).assign(this, itemJson);
            this.productInfo = itemJson.productInfo ? new Product(itemJson.productInfo) : undefined;
            this.price = itemJson.price ? new Price(itemJson.price) : undefined;
        }
    }

    setProductInfo = (productInfo: Product): void => {
        this.productInfo = productInfo;
    };
    getProductInfo = (): Product => {
        return this.productInfo;
    };

    setPrice = (price: Price): void => {
        this.price = price;
    };
    getPrice = (): Price => {
        return this.price;
    };

    setQuantity = (quantity: number): void => {
        this.quantity = quantity;
    };
    getQuantity = (): number => {
        return this.quantity;
    };
}

export class ItemBuilder {
    private item: Item;

    constructor() {
        this.item = new Item();
    }

    withProductInfo(productInfo: Product): ItemBuilder {
        this.item.setProductInfo(productInfo);
        return this;
    }

    withPrice(price: Price): ItemBuilder {
        this.item.setPrice(price);
        return this;
    }

    withQuantity(quantity: number): ItemBuilder {
        this.item.setQuantity(quantity);
        return this;
    }

    build(): Item {
        return this.item;
    }
}
